import { toISOString } from './format';
// Takes two Date objects and determines if they are the same day.
export var isSameDay = function (queryDate, currentDate) {
    return (queryDate.toISOString().slice(0, 10) ===
        currentDate.toISOString().slice(0, 10));
};
export var checkDateTimeMatch = function (a, b) {
    return toISOString(a) === toISOString(b);
};
