export function formatTime(datetime) {
    var date = new Date(datetime);
    return date.toLocaleTimeString([], { hour: 'numeric', minute: '2-digit' });
}
export var formatDate = function (date) {
    var dateArray = date.toDateString().split(' ');
    return "".concat(dateArray[1], " ").concat(dateArray[2], ", ").concat(dateArray[3]);
};
export var formatDateForAPI = function (date) {
    return date.toISOString().slice(0, 10);
};
export var toISOString = function (datestring) {
    return new Date(datestring).toISOString();
};
